.home {
    position: relative;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.profile__container {
    width: 200px;
    height: 200px;
    border-radius: 50%; /* Makes the div circular */
    overflow: hidden; /* Ensures the image stays within the circle */
    display: flex; /* Centers the image */
    justify-content: center; /* Centers the image horizontally */
    align-items: center; /* Centers the image vertically */
    border: 2px solid #ccc;
}
  
.profile-container img {
    width: 100%; /* Ensure the image covers the circle */
    height: 100%; /* Maintain circle proportions */
    object-fit: cover; /* Keeps the image aspect ratio and fills the circle */
}

.intro {
    display: flex;
    flex-direction: column; /* Stack items vertically */
    align-items: center; /* Align items horizontally at the center */
    justify-content: center; /* Align items vertically (optional, for full-height centering) */
    height: 100vh; /* Make the container take the full viewport height (optional) */
    text-align: center; /* Center-align text inside */
    gap: 1rem; /* Add spacing between items */
}

.home__img {
    margin-bottom: 1.5rem;
}

.home__name {
    font-size: var(--h1-font-size);
    font-weight: var(--font-bold);
}

.home__socials {
    display: flex;
    column-gap: 2.5rem;
    margin: 1.5rem 0;
    align-items: center;
    justify-content: center;
}

.home__social-link {
    color: var(--title-color);
    font-size: 1.3rem;
    transition: .3s;
}

.home__social-link:hover {
    color: hsl(43, 100%, 68%);
}

.scroll__down {
    position: absolute;
    bottom: 2.5rem;
    left: 0;
    width: 100%;
}

.home__scroll-name {
    font-size: var(--small-font-size);
}

.mouse {
    border: 2px solid #454360;
    display: block;
    height: 1.6rem;
    width: 1.25rem;
    margin: auto;
    margin-top: .75rem;
    border-radius: 1rem;
    position: relative;
}

@keyframes ani-mouse {
    0% {
        top: 29%;
    }

    15% {
        top: 50%;
    }

    50% {
        top: 50%;
    }

    100% {
        top: 29%;
    }    
}

.wheel {
    background-color: var(--title-color);
    border-radius: 100%;
    width: 0.25rem;
    height: 0.25rem;
    position: absolute;
    top: .5rem;
    left: 50%;
    transform: translateX(-50%);
    animation: ani-mouse 2s linear infinite;
}

.shapes {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
}

